.menu {
  box-sizing: border-box;
  height: 100vh;
  padding: $headerTopPadding 0;

  background-color: $menu-color;
  overflow-y: auto;

  // &__content {

  // }

  &__top {
    @include top-actions;
    margin-bottom: 56px;
  }

  &__list {
    margin: 0 0 40px;
    padding: 0;
    list-style: none;
  }

  &__item {
    margin-bottom: 24px;

    &:last-child {
      margin-bottom: 0;
    }
  }

  &__link {
    display: inline-block;
    padding-bottom: 8px;
    border-bottom: 1px solid transparent;
    font-weight: 700;
    font-size: 22px;
    color: $text-color;
    text-decoration: none;
    line-height: 22px;
    letter-spacing: 2px;
    text-transform: uppercase;

    transition: border-color 0.3s;

    &:hover {
      border-color: $text-color;
    }
  }

  &__call {
    display: block;
    width: max-content;
    font-weight: 700;
    color: $text-color;
    text-decoration: none;

    &--phone {
      margin-bottom: 16px;
      font-size: 16px;
      line-height: 27px;
    }
    &--action {
      padding-bottom: 8px;
      border-bottom: 1px solid $text-color;
      font-size: 12px;
      line-height: 16px;
      letter-spacing: 2px;
      text-transform: uppercase;
    }
  }
}
